<template>
    <div class="camera">
        <div class="lens" :style="{ backgroundImage: 'url(' + images[currentNumber % images.length] + ')' }" />
        <img src="../assets/cameralens.png" alt="camera lens" />
    </div>
</template>

<script>
    export default {
        name: 'CameraLens',
        data() {
            return {
                images: [
                    'https://i.imgur.com/fuM285v.jpg', // group shot
                    'https://i.imgur.com/SJKnmyd.jpg', // toga
                    'https://i.imgur.com/zTpxTjU.jpg', // ochaco,
                    'https://i.imgur.com/W17bbst.jpg' // camie
                ],
                currentNumber: 0,
                timer: null,
            }
        },
        mounted() {
            this.startRotation()
        },
        methods: {
            startRotation: function() {
                this.timer = setInterval(this.next, 3000)
            },
            next: function() {
                this.currentNumber += 1
            }
        }
    }
</script>

